export default {
	props: {
		iconClass: {
			type: String,
			default: 'icon-play'
		},
		disable: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		handleClick() {
			if(this.disable) {
				return
			}
			// 震动功能
			uni.vibrateShort({
				success: function () {
						console.log('success');
				}
			})
			this.$emit('click')
		}
	}
}
